<template>
  <div class="LifeProtection">
    <p class="liftProtection-title">แผนความคุ้มครองชีวิตที่คุณต้องการ</p>
    <div class="tabMain">
      <span
        :class="defaultMain == index ? 'checkTabMain' : 'uncheckTabMain'"
        v-for="(item,index) in tabButtonText"
        :key="index"
        @click="switchMain(index)"
      >{{ item.textName }}</span>
    </div>
    <div class="baseMain" v-show="baseMainShow">
      <p>
        <span>1. คุณอยากเตรียมเงินไว้ให้คนข้างหลังเท่าไร</span>
        <aia-input
          maxlength="22"
          type="number"
          @keydown.native="channelInputLimit"
          v-model="allPreparation"
        ></aia-input>
      </p>
      <p>
        <span>2. คุณเตรียมไว้แล้วเท่าไร</span>
        <aia-input maxlength="22" type="number" v-model="existingPreparation"></aia-input>
      </p>
      <p>
        <span>3. คุณต้องเตรียมเพิ่มอีก</span>
        <aia-input :disabled="true" type="text" v-model="AddPreparation"></aia-input>
      </p>
    </div>
    <div class="seniorMain" v-show="seniorMainShow">
      <p class="liftProtection-title">มาตรฐาน</p>
      <p class="liftProtection-title">แผนการเงินที่คุณควรเตรียมเพื่อคนข้างหลัง</p>
      <div class="specificMain">
        <div class="monthlyMain">
          <p class="liftProtection-title">ค่าใช้จ่ายต่อเดือน</p>
          <p>
            <span>1. ค่าใช้จ่ายที่ต้องใช้ต่อ(Expense per month)</span>
            <aia-input type="number" v-model="expensePerMonth"></aia-input>
          </p>
          <p>
            <span>2. ต้องการเตรียมไว้ให้กี่ปี (Preparation year)</span>
            <aia-input type="number" v-model="preparationYear"></aia-input>
          </p>
        </div>
        <div class="storageMain">
          <p class="liftProtection-title">เงินก้อนสำรอง</p>
          <p>
            <span>1. เงินก้อนเพื่อชำระหนี้(Money for debt)</span>
            <aia-input type="number" v-model="MoneyForDebt"></aia-input>
          </p>
          <p>
            <span>2. เงินเพื่อการกุศล (Last expense)</span>
            <aia-input type="number" v-model="LastExpense"></aia-input>
          </p>
          <p>
            <span>3. ค่าใช้จ่ายครั้งสุดท้าย (Money for charity)</span>
            <aia-input type="number" v-model="MoneyForCharity"></aia-input>
          </p>
          <p>
            <span>4. เงินก้อนสำรองเพิ่มเติม(Contingency money)</span>
            <aia-input type="number" v-model="ContingencyMoney"></aia-input>
          </p>
        </div>
      </div>
      <div style="margin-top:0.1rem">
        <p>Money plan</p>
        <aia-input type="number" :disabled="true" v-model="MoneyPlan"></aia-input>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  nanme: "LifeProtection",
  data() {
    return {
      defaultMain: 0,
      baseMainShow: true,
      seniorMainShow: false,
      allPreparation: "",
      existingPreparation: "",
      expensePerMonth: "",
      preparationYear: "",
      MoneyForDebt: "",
      LastExpense: "",
      MoneyForCharity: "",
      ContingencyMoney: "",
      // MoneyPlan: "",
      tabButtonText: [
        {
          textName: "แบบ"
        },
        {
          textName: "ยิ่งขึ้น"
        }
      ]
    };
  },
  computed: {
    AddPreparation() {
      if (this.allPreparation - this.existingPreparation < 0) {
        return "0.00";
      } else {
        return this.allPreparation - this.existingPreparation;
      }
    },
    MoneyPlan() {
      let MoneyPlanData =
        this.expensePerMonth * this.preparationYear +
        this.MoneyForDebt * 1 +
        this.MoneyForCharity * 1 +
        this.LastExpense * 1 +
        this.ContingencyMoney * 1;
      if (MoneyPlanData < 0) {
        return "0.00";
      } else {
        return MoneyPlanData;
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    this.saveDataToOC();
    next();
  },
  methods: {
    channelInputLimit(e) {
      let key = e.key;
      // 不允许输入'e'和'.'
      if (key === "e" || key === ".") {
        e.returnValue = false;
        return false;
      }
      return true;
    },
    switchMain(index) {
      this.defaultMain = index;
      if (index == 0) {
        this.baseMainShow = true;
        this.seniorMainShow = false;
      } else {
        this.baseMainShow = false;
        this.seniorMainShow = true;
      }
      this.saveDataToOC();
    },
    saveDataToOC() {
      let _this = this;
      // console.log({
      //   defaultMain: _this.defaultMain,
      //   baseMainShow: _this.baseMainShow,
      //   seniorMainShow: _this.seniorMainShow,
      //   allPreparation: _this.allPreparation,
      //   existingPreparation: _this.existingPreparation,
      //   expensePerMonth: _this.expensePerMonth,
      //   preparationYear: _this.preparationYear,
      //   MoneyForDebt: _this.MoneyForDebt,
      //   LastExpense: _this.LastExpense,
      //   MoneyForCharity: _this.MoneyForCharity,
      //   ContingencyMoney: _this.ContingencyMoney,
      //   MoneyPlan: _this.AddPreparation
      // });
      alert(
        JSON.stringify({
          defaultMain: _this.defaultMain,
          baseMainShow: _this.baseMainShow,
          seniorMainShow: _this.seniorMainShow,
          allPreparation: _this.allPreparation,
          existingPreparation: _this.existingPreparation,
          expensePerMonth: _this.expensePerMonth,
          preparationYear: _this.preparationYear,
          MoneyForDebt: _this.MoneyForDebt,
          LastExpense: _this.LastExpense,
          MoneyForCharity: _this.MoneyForCharity,
          ContingencyMoney: _this.ContingencyMoney,
          MoneyPlan: _this.AddPreparation
        })
      );

      WebViewJavascriptBridge.callHandler(
        "saveLifeProtectionData",
        {
          defaultMain: _this.defaultMain,
          baseMainShow: _this.baseMainShow,
          seniorMainShow: _this.seniorMainShow,
          allPreparation: _this.allPreparation,
          existingPreparation: _this.existingPreparation,
          expensePerMonth: _this.expensePerMonth,
          preparationYear: _this.preparationYear,
          MoneyForDebt: _this.MoneyForDebt,
          LastExpense: _this.LastExpense,
          MoneyForCharity: _this.MoneyForCharity,
          ContingencyMoney: _this.ContingencyMoney,
          MoneyPlan: _this.MoneyPlan
        },
        function(response) {
          alert(response);
        }
      );
    }
  }
};
</script>
